<template>
    <div class="container">
        <span class="input" @click="dialogVisible = true">{{message}}</span>
        <el-dialog v-model="dialogVisible" title="tips">
            
            <div class="msg_body">
                <div class="modal">模块：
                    <div v-for="item in nowCard">
                        <div v-if="item.type == 'card'">
                            <span >{{ item.value }}</span><el-input></el-input>
                        </div>
                        <div v-else-if="item.type == 'select'">
                            <el-select></el-select>
                        </div>
                    </div>
                </div>
                <span>文字</span>
                <div class="card">
                    <span v-for="text in cardList" @click="addCard(text)">{{ text }}</span>
                </div>
                <span>选择框</span>
                <div>
                    <span>日期选择框</span>
                    <el-select>
                        <el-option>本日</el-option>
                        <el-option>本月</el-option>
                        <el-option>本年</el-option>
                    </el-select>
                </div>
                <div>
                    <el-checkbox v-model="check2" />
                    <el-select>
                        <el-option>健康</el-option>
                        <el-option>病危</el-option>
                    </el-select>
                </div>
            </div>
            <template #footer>
                <el-button @click="confirm()">确定</el-button>
                <el-button @click="dialogVisible = false">算了</el-button>
            </template>
        </el-dialog>
    </div>
</template>

<script setup>
import { reactive, ref } from "vue";
    const dialogVisible = ref(false)
    const message = ref('点击输入')
    const cardList = ['姓名','性别代码','出生日期']
    const nowCard = reactive([])
    const confirm = () => {
        message.value = document.getElementsByClassName('mrecord')[0].textContent
    }
    const addCard = (val) => {
        nowCard.push({type:'card',value : val+':'})
    }
</script>
<style lang="less" scoped>
    .container{
        display: flex;
        flex-direction: column;
        align-items: center;
        .msg_body{
            display: flex;
            flex-direction: column;
            .card span{
                cursor: pointer;
                padding: 5px;
            }
        }
    }
</style>

